import React, {useState} from "react";
import "./Uvod.css";
import {BrowserRouter as Router, Link} from 'react-router-dom';
import {Card} from "react-bootstrap";
import Img1 from "../../Images/Sluzby-uvod/obrabeni.jpg";
import Img2 from "../../Images/Sluzby-uvod/zamecnicke.jpg";
import Img3 from "../../Images/Sluzby-uvod/svareni.jpg";
import Img4 from "../../Images/Sluzby-uvod/dokumentace.jpg";
import { LinkContainer } from "react-router-bootstrap";

const Uvod = () => {
    
    return(
        <>
        <div className="bg">
        <h1 className="main-text">Zakzkov obrbn na mru.</h1>
        </div>
        <div className="grid-container">
            <div className="grid-item 1">
                <LinkContainer className="cardsluzby" to={"./obrabeni"}>  
                    <Card>
                        <Card.Img className="cardimg" variant="top"  src={Img1} />
                            <h3 className="cardtext">Obrbn</h3>
                    </Card>
                </LinkContainer>    
            </div>
            <div className="grid-item 2">
            <Link className="cardsluzby" to={"./zamprace"}>  
                    <Card>
                        <Card.Img className="cardimg" variant="top"  src={Img2} />
                            <h3 className="cardtext" id="textbla">Zmenick prce</h3>
                    </Card>
                </Link>  
            </div>
            <div className="grid-item 3">
            <Link className="cardsluzby" to={"./svarovani"}>  
                    <Card>
                        <Card.Img className="cardimg" variant="top"  src={Img3} />
                            <h3 className="cardtext">Svaovn</h3>
                    </Card>
                </Link>  
            </div>
            <div className="grid-item 4">
            <Link className="cardsluzby" to={"./techdokum"}>  
                    <Card>
                        <Card.Img className="cardimg" variant="top"  src={Img4} />
                        <h3 className="cardtext">Technick dokumentace</h3>
                    </Card>
                </Link>  
            </div>
        </div>
        {/*End of sluzby grid*/} 
        <h1 className="sronazev" >VALTES s.r.o</h1>
        <div className="containertext">
            <div className="firsttext">
            Specializujeme se na vrobu soustruench a frzovanch dl dle vaich poteb, tm jsme pro vs schopni zajistit celkov servis.
            </div>
            <div className="lineblue"></div>
            <div className="secondtext">
            Zamujeme se na zakzkovou a malosriovou vrobou soustruench i frzovanch 
            dl z cel ady eleznch, nerezovch i ostatnch typ materil jako je napklad plast a devo.
            </div>
        </div>
        <div className="containertext2">
            <h1 className="vysledku">STROJN VYBAVEN PRO DOSAEN NEJLEPCH VSLEDK</h1>
            <div className="vysledkupad">
                <div className="vysledku1"><div className="dot"></div> Dbme na nae strojn vybaven tak, aby bylo vdy v perfektnm stavu.</div>
                <div className="vysledku2"><div className="dot"></div> Nae obsluha m destky let zkuenost a je odborn zakolena.</div>
                <div className="vysledku3"><div className="dot"></div> Provdme kontrolu kvality vrobk pro dosaen stanovench parametr.</div>
            </div>
        </div>
        <div className="containertext3">
            <h1 className="vysledku">VHODY SPOLUPRCE S NMI</h1>
            <div className="vysledkupad">
                <div className="vysledku1"><div className="dot"></div> Naim zkaznkm se vnujeme po celou dobu spoluprce kdykoliv je poteba.</div>
                <div className="vysledku2"><div className="dot"></div> Disponujeme vlastnm vybavenm, kter mme kdykoliv k dispozici</div>
                <div className="vysledku3"><div className="dot"></div> Vechny zakzky peliv zpracovvme a ke kad pistupujeme individuln a flexibiln.</div>
            </div>
        </div>
        </>
    );
}
export default Uvod;