import React from "react";
import "../Sluzby.css";
import Img1 from "../../../Images/Zamprace/zamprace1.png";

const Zamprace = () => {
    return(<>
        <div className="obrabeni">
            <h1 className="obrabeni-main">Zmenick prce</h1>
            <div className="grid2-container">
                <div className="grid2-item-img">
                    <img className="obrabeni-img" alt="" src={Img1}/>
                </div>
                <div className="grid2-item-text">
                    <div className="text-sluzba2">Roziujcm spektrem na vroby jsou zmenick prce, kdy pro nae zkaznky zajiujeme
                        opracovn dl dle stanovench poadavk.
                    </div>
                </div>
            </div>
        </div>
        <div className="containertext3">
            <h1 className="vysledku">STROJN VYBAVEN PRO DOSAEN NEJLEPCH VSLEDK</h1>
            <div className="vysledkupad">
                <div className="vysledku1"><div className="dot"></div> Dbme na nae strojn vybaven tak, aby bylo vdy v perfektnm stavu.</div>
                <div className="vysledku2"><div className="dot"></div> Nae obsluha m destky let zkuenost a je odborn zakolena.</div>
                <div className="vysledku3"><div className="dot"></div> Provdme kontrolu kvality vrobk pro dosaen stanovench parametr.</div>
            </div>
        </div>
        </>
    );
}
export default Zamprace;